<template>
  <div>
    <h1>サーバー詳細</h1>
    <div v-if="server" class="content" id="eee">
      <p>サーバー名: {{ server.displayServerName }}</p>
      <p>サーバー名: {{ server.serverData.players.length }}</p>
      <ul v-if="server.serverData.players.length > 0">
        <li v-for="(player, index) in server.serverData.players" :key="index">
          {{ player.name }}
        </li>
      </ul>
    </div>
    <div v-else>
      <p>サーバー情報が見つかりません。</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      server: null,
    };
  },
  computed: {
    serverKey() {
      return this.$route.params.serverKey; // URLパラメータからserverKeyを取得
    },
  },
  watch: {
    serverKey(newKey) {
      // サーバー情報を取得して設定
      this.fetchServerDetails(newKey);
    },
  },
  methods: {
    fetchServerDetails(serverKey) {
      const server = this.$store.getters.getServerByKey(serverKey);
      if (server) {
        this.server = server;
      } else {
        this.server = null; // サーバーが見つからない場合
      }
    },
  },
  mounted() {
    this.fetchServerDetails(this.serverKey); // 初期表示時にサーバー情報を取得
  },
};
</script>

<style>
  .content {
    color: white;
    text-align: center;
  }
</style>